.flatCard-main {
    padding: 1rem;
    box-shadow: var(--box-shadow);
    border-radius: 5px;
    transition: var(--transition);
    margin: auto;
 }
 
 #dark .flatCard-main {
    box-shadow: var(--box-shadow-dark);
 }
 
 @media (max-width : 600px) {
    .flatCard-main {
       width: 100%;
       padding: .5rem;
    }
 }