#skill {
 
    background-repeat: no-repeat;
    background-position-y: 100%;
    background-size: 20%;
    transition: var(--transition);
 }
 
 .my-skills-container {
    width: 70%;
    display: flex;
    gap: 40px;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin: auto;
    padding: 2rem;
 }
 
 @media (max-width : 900px) {
    #skill {
       background-size: 50% !important;
    }
 
    .my-skills-container {
       width: 100% !important;
    }
 }